import { NavLink } from 'react-router-dom'
import css from './headerAdmin.module.scss'

const HeaderAdmin = ({isLogin, onLogout}) => {
    return(
        <div className={css.header}>
            <h2>Eleganto</h2><h4> v0.2 beta</h4>
            <h3 className={css.adminpanelname}>Admin Panel - SmileA</h3>
            {isLogin ? <div className={css.headerMenu}>
                <h4>Общее</h4>
                <NavLink to="/adminpage/mainpage" activeClassName={css.selectedLink}>Главная</NavLink>
                <NavLink to="/adminpage/start" activeClassName={css.selectedLink}>Быстрый старт</NavLink>
                <NavLink to="/adminpage/manual" activeClassName={css.selectedLink}>Мануал по работе</NavLink>
            </div> : null }
            {isLogin ? <div className={css.headerMenu}>
                <h4>База данных</h4>    
                <NavLink to="/adminpage/services" activeClassName={css.selectedLink}>Список услуг</NavLink>
                <NavLink to="/adminpage/discounts" activeClassName={css.selectedLink}>Список акций</NavLink>
                <NavLink to="/adminpage/forms" activeClassName={css.selectedLink}>Список заявок</NavLink>
            </div> : null}
            {isLogin ? <div className={css.headerMenu}>
                <h4>Профиль: админ</h4>
                <NavLink to="/adminpage" onClick={() => onLogout()}>Выйти</NavLink>
            </div> : null}
            
            
        </div>
    );
}

export default HeaderAdmin;