import { useEffect } from 'react';
import css  from './about.module.scss' ;

const About = ({changePage}) => {
    useEffect(() => {
        document.title = "О стоматологии | Стоматология Смайл-А в городе Санкт-Петербурге"
        changePage("About")
    }, [])
    return(
        <div className={css.about}>
            <div className={`container ${css.aboutContainer}`}>
                <div className={css.aboutTitleBox}>
                    <h1>О стоматологии</h1>
                </div>
                <div className={css.aboutBlock}>
                    <div className={css.aboutBlockText}>Стоматологическая клиника Смайл-А. Наша забота - Ваша улыбка!</div>
                    <div className={css.aboutBlockImage} id={css.image1}></div>
                </div>
                <div className={css.aboutBlock}>
                    <div className={css.aboutBlockText}>В своей работе стоматологический центр Смайл-А ориентируется на передовые мировые стандарты стоматологического лечения. На их основе для своих специалистов мы устанавливаем собственные высокие требования, которые ставят нашу клинику в один ряд с клиниками мирового уровня, сохраняя при этом адекватные цены.</div>
                    <div className={css.aboutBlockImage} id={css.image2}></div>
                </div>
                <div className={css.aboutBlock}>
                    <div className={css.aboutBlockText}>Наша клиника специализируется на всех видах стоматологических услуг: диагностике и лечении зубов с использованием стоматологического микроскопа, всех видах хирургической стоматологии и имплантации, протезировании, ортодонтии и отбеливании зубов.</div>
                    <div className={css.aboutBlockImage} id={css.image3}></div>
                </div>
                <div className={css.aboutBlock}>
                    <div className={css.aboutBlockText}>Стоматологическая клиника "Смайл-А" имеет в своём арсенале самое новое оборудование и современные материалы для оказания всех видов стоматологической помощи.</div>
                    <div className={css.aboutBlockImage} id={css.image4}></div>
                </div>
            </div>
        </div>
    );
};

export default About;

