import css from './loginAdmin.module.scss'
import { Button, Form, Segment, Header, Message } from 'semantic-ui-react'
import { useState } from 'react'
import useHttp from './../../../hooks/http.hook'

const LoginAdmin = ({onLogin}) => {
    const {loader, request, error} = useHttp();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [isFalse, setIsFalse] =useState(false);
    const [isLoading, setIsLoading] =useState(false);

    const requestLogin = async () => {
        setIsFalse(false);
        setIsLoading(true);
        try {
            const sendData = {username: username, password: password}
            const data = request('/api/auth/login', 'POST', {...sendData})
        } catch (e) {

        }

        if (username === 'admin2020' && password === '123321') {
            // onLogin();
            // setIsLoading(false);
        } else {
            setIsFalse(true);
            // setIsLoading(false);
        }

    }

    const closeMessage = () => {
        setIsFalse(false)
    }

    
    return (
        <div className={css.loginForm}>
            <Segment>
                <Header as='h2' floated='left'>
                Необходимо авторизоваться для просмотра панели администрирования!
                </Header>
                    <Form>
                        <Form.Input
                            icon='user'
                            iconPosition='left'
                            label='Username'
                            placeholder='Username'
                            value={username}
                            onChange={(event) => setUsername(event.target.value)}
                        />
                        <Form.Input
                            icon='lock'
                            iconPosition='left'
                            label='Password'
                            type='password'
                            value={password}
                            onChange={(event) => setPassword(event.target.value)}
                        />
                        {isFalse ? <Message negative onClick={() => closeMessage()}>
                                        <Message.Header>Ошибка доступа</Message.Header>
                                        <p>
                                        Неправильный логин или пароль
                                        </p>
                                    </Message> : null}
                        <Button disabled={isLoading} content='Login' primary onClick={() => requestLogin()}/>
                    </Form>

            </Segment>
        </div>
    );
}

export default LoginAdmin;