import './App.scss';

import HeaderMain from './components/header/header';
import Content from './components/content/content';
import Footer from './components/footer/footer';
import FormPage from './components/formPage/formPage';
import { useState } from 'react';
import MobileMenu from './components/mobileMenu/mobileMenu';



function App() {
  const [isForm, setIsForm] = useState(false); //fuck form
  const [isMobileMenu, setIsMobileMenu] = useState(false);
  const [selectedPage, setSelectedPage] = useState("Main");

  const changePage = (page) => {
    setSelectedPage(page);
  }

  const openMobileMenu = (event) => {
    if (!isMobileMenu) {
      document.body.style.position='fidex';
    document.body.style.top=`-${window.scrollY}px`;
    document.body.style.overflow=`hidden`;
    } else {
      setIsForm(false);
      document.body.style.position='static';
      document.body.style.top=`-${window.scrollY}px`;
      // window.scrollTo(0, parseInt(window.scrollY || '0') * -1); // не трогать, а то сломается
      document.body.style.overflow=`auto`;
    }
    setIsMobileMenu(!isMobileMenu);
    console.log('Mobile');
  }

  const formOpen = (event) => {
    document.body.style.position='fidex';
    document.body.style.top=`-${window.scrollY}px`;
    document.body.style.overflow=`hidden`;
    setIsForm(true);
    event.preventDefault();
  }
  const formClose = () => {
    // const top = document.body.style.top;
    setIsForm(false);
    document.body.style.position='static';
    document.body.style.top=`-${window.scrollY}px`;
    // window.scrollTo(0, parseInt(window.scrollY || '0') * -1); // не трогать, а то сломается
    document.body.style.overflow=`auto`;
  }

    
  return (
    <div className='AppWrapper'>
      <HeaderMain isMobileMenu={isMobileMenu} openMobileMenu={openMobileMenu} selectedPage={selectedPage}/>
      {isMobileMenu ? <MobileMenu openMobileMenu={openMobileMenu} selectedPage={selectedPage}/> : null}
      <Content changePage={changePage}/>
      {isForm ? <FormPage isForm={isForm} formClose={formClose} /> : null }
      <Footer />
      {window.location.pathname !== "/adminpage/" && isForm === false ? <button onClick={(event) => formOpen(event)} className="formButton formButtonFixed">
          <div className="phoneLogo" id="formButton"></div> <span>Оставить заявку</span>
      </button> : null } {/* shit condition */}
    </div>
  );
}

export default App;
