import css from './mobileMenu.module.scss'

const MobileMenu = ({openMobileMenu}) => {
    return(
        <div className={css.mobileMenuOverlay}>
            <div className={css.mobileMenu}>
                <div className={css.mobileMenuRow}><a href="/#/about" onClick={() => openMobileMenu()}>О стоматологии</a></div>
                <div className={css.mobileMenuRow}><a href="/#/Specialists" onClick={() => openMobileMenu()}>Наши специалисты</a></div>
                <div className={css.mobileMenuRow}><a href="/#/pricelist" onClick={() => openMobileMenu()}>Цены и услуги</a></div>
                {/* <div className={css.mobileMenuRow}><a href="/discounts">Акции</a></div> */}
                <div className={css.mobileMenuRow}><a href="/#/reviews" onClick={() => openMobileMenu()}>Отзывы</a></div>
                <div className={css.mobileMenuRow}><a href="/#/contacts" onClick={() => openMobileMenu()}>Контакты</a></div>
            </div>
        </div>
        
    );

}

export default MobileMenu