import { Switch, Route } from "react-router-dom";
import css from './content.module.scss';

import Contacts from "../contacts/contacts.jsx";
import Main from "../main/main.jsx";
import PriceList from "../priceList/priceList";
import About from "../about/about";
import OurSpecialists from "../ourSpecialists/ourSpecialists";
import Reviews from "../Reviews/reviews";
import PersonalData from "../personalData/personalData";

const Content = ({changePage}) => {


    return(
        <div className={css.content}>
            <Switch>
                <Route exact path="/">
                    <Main changePage={changePage}/>
                </Route>
                <Route path="/about">
                    <About changePage={changePage}/>
                </Route>
                <Route path="/pricelist">
                    <PriceList changePage={changePage}/>
                </Route>
                {/* <Route path="/discounts">
                    <p>Discounts</p>
                </Route> */}
                <Route path="/contacts">
                    <Contacts changePage={changePage}/>
                </Route>
                <Route path="/specialists">
                    <OurSpecialists changePage={changePage}/>
                </Route>

                <Route path="/reviews">
                    <Reviews changePage={changePage}/>
                </Route>
                <Route path="/confidence">
                    <PersonalData changePage={changePage}/>
                </Route>
            </Switch>
        </div>
    );
};

export default Content;

