import css from './contentAdmin.module.scss'

import LoginAdmin from '../loginAdmin/loginAdmin'
import { Redirect, Route, Switch } from 'react-router';
import MainAdminPage from '../mainAdminPage/mainAdminPage';
import ProductBasePage from '../productsBasePage/productsBasePage';


const ContentAdmin = ({isLogin, onLogin}) => {
    

    return (
        <div className={css.content}>
            {!isLogin ? <LoginAdmin onLogin={onLogin}/> :
                <Switch>
                    <Route exact path="/adminpage">
                        <Redirect to="/adminpage/mainpage" />
                    </Route>
                    <Route path="/adminpage/mainpage">
                        <MainAdminPage />
                    </Route>
                    <Route path="/adminpage/services">
                        <ProductBasePage />
                    </Route>
                </Switch>
              
            }

        </div>
    );
}

export default ContentAdmin;