import { List, Segment, Header } from 'semantic-ui-react'
import css from './mainAdminPage.module.scss'

const MainAdminPage = () => {
    return(
        <div className={css.mainAdmin}>
            <Segment>
                <Header as="h2" float="left">
                    Добро пожаловать в панель администрирования!
                </Header>
            </Segment>
            <Segment>
                <Header as="h3" float="left">
                    Обновления системы v0.2 beta:
                </Header>
                <Header as="h4" float="left">
                    Добавлено:
                </Header>
                <List bulleted>
                    <List.Item>Управление Базами Данных</List.Item>
                    <List.Item>Мануал по работе с ПО Eleganto </List.Item>
                    <List.Item>Функционал для менеджеров по заявкам</List.Item>
                </List>
                <Header as="h4" float="left">
                    Обновлено:
                </Header>
                <List bulleted>
                    <List.Item>Дизайн ПО</List.Item>
                    <List.Item>Баг с авторизацией, который при обновлении страницы не запоминал состояние</List.Item>
                    <List.Item>Обновлена главная страница, добавлены changelogs</List.Item>
                </List>
            </Segment>

        </div>
    );
}

export default MainAdminPage;