import { useEffect } from 'react';
import css  from './contacts.module.scss' ;

const Contacts = ({changePage}) => {
    useEffect(() => {
        document.title = "Контакты | Стоматология Смайл-А в городе Санкт-Петербурге"
        changePage("Contacts")
    }, [])

    return(
        <div className={css.contacts}>
            <div className={`container ${css.contactsContainer}`}>
                <div class={css.contactsTitleBox}>
                    <h1>Контакты</h1>
                </div>
                <div className={css.contactsBox}>
                    <div className={css.contactsBoxInfo}>
                        <div className={css.contactsBoxInfoAdress}>
                            <div className={css.contactsBoxInfoAdressName}>Ждём вас по адресу:</div>
                            <div className={css.contactsBoxInfoAdressWord}>Санкт-Петербург, пр. Луначарского, д.64</div>
                        </div>
                        <div className={css.contactsBoxInfoPhone}>
                            <div className={css.contactsBoxInfoPhoneNumberWord}>Наш телефон:</div>
                            <div className={css.contactsBoxInfoPhoneNumber}> +7(812)516-00-27</div>
                        </div>
                        <div className={css.contactsBoxInfoAbout}>Наш график работы:</div>
                        <div className={css.contactsBoxInfoTime}>
                            <div className={css.contactsBoxInfoTimeWeek}>
                                <div className={css.contactsBoxInfoTimeWeekDays}>С понедельника по пятницу:</div>
                                <div className={css.contactsBoxInfoTimeWeekWatch}>с 09:00 до 20:00 часов.</div>
                            </div>
                            <div className={css.contactsBoxInfoTimeSut}>
                                <div className={css.contactsBoxInfoTimeSutDay}>В субботу мы работаем с:</div>
                                <div className={css.contactsBoxInfoTimeSutWatch}> 10:00 до 15:00 часов.</div>
                            </div>
                            <div className={css.contactsBoxInfoTimeSun}>
                                <div className={css.contactsBoxInfoTimeSunDay}>В воскресенье мы работаем с:</div>
                                <div className={css.contactsBoxInfoTimeSunWatch}>11:00 до 16:00 часов .</div>
                            </div>
                        </div>
                    </div>

                    <div className={css.contactsMap}>
                        <iframe src="https://yandex.ru/map-widget/v1/?um=constructor%3A0d40cc1e48015ce7eea952d98de51511824833824372f3096bdfc6fdb70ee940&amp;source=constructor" 
                         frameborder="0"></iframe>
                    </div>
                </div>
            </div>

        </div>
    );
};

export default Contacts;