import css from './adminPage.module.scss'
import 'semantic-ui-css/semantic.min.css'
import {useState} from 'react'

import HeaderAdmin from './headerAdmin/headerAdmin'
import ContentAdmin from './contentAdmin/contentAdmin'
import {useAuth} from "../../hooks/auth.hook";


const AdminPage = () => {
    const {token, login, logout, userId} = useAuth();

    const [isLogin, setIsLogin] = useState((localStorage.getItem('isLogin') === "true" ? true : false));

    const onLogin = () => {
        setIsLogin(true);
        localStorage.setItem('isLogin', 'true');
    }
    const onLogout = () => {
        setIsLogin(false);
        localStorage.setItem('isLogin', 'false');
    }
    return(
        <div className={css.adminWrapper}>
            <HeaderAdmin isLogin={isLogin} onLogout={onLogout}/>
            <ContentAdmin isLogin={isLogin} onLogin={onLogin}/>
        </div>
    );
}

export default AdminPage;