import { useEffect } from "react"
import css from "./ourSpecialists.module.scss"

const OurSpecialists = ({changePage}) => {
    useEffect(() => {
        document.title = "Наши специалисты | Стоматология Смайл-А в городе Санкт-Петербурге"
        changePage("Specialists")
    }, [])
    return (
        <div className={css.specialists}>
            <div className={`container ${css.specialistsContainer}`}>
                <div className={css.specialistsTitleBox}>
                    <h1>Наши специалисты</h1>
                </div>
                <div className={css.specialistsList}>
                    <div className={css.specialistsBox}>
                        <div className={css.specialistsBoxInfo}>
                            <div className={css.specialistsBoxInfoName}>Цыварева Екатерина Евгеньевна</div>
                            <div className={css.specialistsBoxInfoAbout}>Врач - стоматолог - ортодонт.</div>
                        </div>
                        <div className={css.specialistsBoxPicture} id={css.spimage1}></div>
                    </div>
                    <div className={css.specialistsBox}>
                        <div className={css.specialistsBoxInfo}>
                            <div className={css.specialistsBoxInfoName}>Бородулин Владимир Николаевич</div>
                            <div className={css.specialistsBoxInfoAbout}>Врач - стоматолог, хирург - ипмлантолог высшей категории, опыт работы 30 лет.</div>
                        </div>
                        <div className={css.specialistsBoxPicture} id={css.spimage2}></div>
                    </div>
                    <div className={css.specialistsBox}>
                        <div className={css.specialistsBoxInfo}>
                            <div className={css.specialistsBoxInfoName}>Сидоренко Евгения Александровна</div>
                            < div className={css.specialistsBoxInfoAbout}>Врач - стоматолог - терапевт. Опыт работы 20 лет.</div>
                        </div>
                        <div className={css.specialistsBoxPicture} id={css.spimage3}></div>
                    </div>
                    <div className={css.specialistsBox}>
                        <div className={css.specialistsBoxInfo}>
                            <div className={css.specialistsBoxInfoName}>Зверькова Татьяна Михайловна</div>
                            < div className={css.specialistsBoxInfoAbout}>Врач - стоматолог - терапевт.</div>
                        </div>
                        <div className={css.specialistsBoxPicture} id={css.spimage4}></div>
                    </div>
                    <div className={css.specialistsBox}>
                        <div className={css.specialistsBoxInfo}>
                            <div className={css.specialistsBoxInfoName}>Аванян Акоп Яковлевич</div>
                            < div className={css.specialistsBoxInfoAbout}>Врач - стоматолог - ортопед.</div>
                        </div>
                        <div className={css.specialistsBoxPicture} id={css.spimage5}></div>
                    </div>
                </div>
            </div>
        </div>

       
    )
}

export default OurSpecialists