import css from './main.module.scss'

export const MainInfo = () => {
    return (
        <div className={css.MainInfo}>
            <div className={`${css.MainInfoContainer} container`}>
                <div className={css.MainInfoLogotype}>
                    <div className={css.MainInfoLogotypeImage}></div>
                </div>
                <div className={css.MainInfoLogotypeText}>
                    <span className={css.MainInfoLogotypeTextTitle}>Добро пожаловать в стоматологическую клинику Смайл-А</span>
                    <span className={css.MainInfoLogotypeTextDesc}></span>
                    <div className={css.MainInfoAdressRow}>
                        <div className={css.MainInfoAdress}>
                            <div className={css.MainInfoAdressImage}></div>
                            <p className={css.MainInfoAdressText}>пр. Луначарского 64</p>
                        </div>
                        <div className={css.MainInfoPhone}>
                            <div className={css.MainInfoPhoneImage}></div>
                            <p className={css.MainInfoPhoneText}>+7(812) 516-00-27</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}