import { useEffect } from 'react';
import css  from './reviews.module.scss';
/* global VK */

const Reviews = () => {
    useEffect(() => {
        document.title = "Отзывы | Стоматология Смайл-А в городе Санкт-Петербурге"
    }, [])
    return(
        <div className={css.reviews}>
            <div className={`container ${css.reviewsContainer}`}>
                <div className={css.reviewsTitleBox}>
                    <h1>Отзывы о нас</h1>
                </div>
                <div className={css.reviewsEnterBox}>Здесь вы можете оставить свой отзыв</div>
                <div id="vk_comments"></div>
<script type="text/javascript">
{VK.Widgets.Comments("vk_comments", {limit: 10, attach: "*"})}
</script>
                
            </div>
        </div>
    );
};

export default Reviews;