import {  Segment, Header } from 'semantic-ui-react'
import css from './productsBasePage.module.scss'

const ProductBasePage = () => {
    return(
        <div className={css.mainAdmin}>
            <Segment>
                <Header as="h2" float="left">
                    Управление услугами стоматологии
                </Header>
            </Segment>
            <Segment>
                <Header as='h4' float='left'>Управление категориями</Header>
            </Segment>

        </div>
    );
}

export default ProductBasePage;