import css from './header.module.scss';
import {NavLink} from 'react-router-dom';

const HeaderMain = ({isMobileMenu, openMobileMenu, selectedPage}) => {
    return(
        <div className={css.header}>
            <div className={`container ${css.headerContainer}`}>
                <a href="/#/" className={css.headerLogotype}>
                    <div className={css.headerLogotypeImage}></div>
                    <div className={css.headerLogotypeText}>
                        <span className={css.headerLogotypeTextTitle}>Смайл-А</span>
                        <span className={css.headerLogotypeTextDesc}>стоматология</span>
                    </div>
                </a>
                <div className={css.headerWhiteSpace}></div>
                {selectedPage !== "Main" ? <div className={css.headerContacts}>
                    <div className={css.headerContactsAdress}>
                        <div className={css.headerContactsAdressImage}></div>
                        <p className={css.headerContactsAdressText}>пр. Луначарского 64</p>
                    </div>
                    <div className={css.headerContactsPhone}>
                        <div className={css.headerContactsPhoneImage}></div>
                        <p className={css.headerContactsPhoneText}>+7(812) 516-00-27</p>
                    </div>
                </div>: null}
                <div className={css.headerWhiteSpace}></div>
                <div className={css.headerMenu}>
                    <a href="/#/about" className={css.headerMenuLink}>О стоматологии</a>
                    <a href="/#/specialists" className={css.headerMenuLink}>Наши специалисты</a>
                    <a href="/#/pricelist" className={css.headerMenuLink}>Цены и услуги</a>
                    {/* <a href="/discounts" className={css.headerMenuLink}>Акции</a> */}
                    {/* <a href="/discounts" className={css.headerMenuLink}>Акции</a> */}
                    <a href="/#/reviews" className={css.headerMenuLink}>Отзывы</a>
                    <a href="/#/contacts" className={css.headerMenuLink}>Контакты</a>
                </div>
                <div className={isMobileMenu ? css.headerMobileClose : css.headerMobile } onClick={(event) => openMobileMenu(event)}></div>
            </div>
        </div>  
    );

}

export default HeaderMain;