import { useState } from 'react';
import css from './formPage.module.scss';
import { IMaskInput } from 'react-imask';

const FormPage = ({isForm, formClose}) => {
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [email, setEmail] = useState('');
    const [phoneNumber, setPhoneNubmer] = useState('');
    const [dateVisit, setDateVisit] = useState('');
    const [timeVisit, setTimeVisit] = useState('');
    const [goalVisit, setGoalVisit] = useState('');
    const [isAgreed, setIsAgreed] = useState(false);

    const checkboxHandler = (event) => {
        setIsAgreed(isAgreed ? false : true);
    }


    const submitForm = async () => {
        let data = {}
        data.firstName = firstName;
        data.lastName = lastName;
        data.email = email;
        data.phoneNumber = phoneNumber;
        data.dateVisit = dateVisit;
        data.timeVisit = timeVisit;
        data.goalVisit = goalVisit;
        let dataMessage = 'Получена новая заявка: %0A';
        dataMessage = dataMessage + `Имя: ${firstName}%0A`;
        dataMessage = dataMessage + `Фамилия: ${lastName}%0A`;
        dataMessage = dataMessage + `Почта: ${email}%0A`;
        dataMessage = dataMessage + `Телефон: ${phoneNumber}%0A`;
        dataMessage = dataMessage + `Дата визита: ${dateVisit}%0A`;
        dataMessage = dataMessage + `Время: ${timeVisit}%0A`;
        dataMessage = dataMessage + `Цель: ${goalVisit}%0A`;
        console.log(dataMessage);

        console.log(data);
        console.log(JSON.stringify(data));
        const response = await fetch(`https://api.telegram.org/bot1665529045:AAHxO9W4D19v9H2grpocRW-1MGMfiaAjXX4/sendMessage?chat_id=-578991370&parse_mode=html&text=${dataMessage}`, {
            method: "POST",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(data),
        });

        if (response.ok) {formClose();}

    }

    return(
        <div className={ isForm ? css.form : css.hide }>
            <div className={`container ${css.formContainer}`}>
                <div className={ isForm ? css.formBox : css.hide }>
                    <div className={css.formBoxHeader}>
                        <button className={css.formBoxClose} onClick={() => formClose()}></button>
                    </div>
                    <div className={css.formBoxBody}>
                        <h1>Создание заявки</h1>
                        <input type="text" className={css.formInput} placeholder="Имя" value={firstName} onChange={(event) => setFirstName(event.target.value)}/>
                        <input type="text" className={css.formInput} placeholder="Фамилия" value={lastName} onChange={(event) => setLastName(event.target.value)}/>
                        <input type="email" className={css.formInput} placeholder="Электронная почта" value={email} onChange={(event) => setEmail(event.target.value)}/>
                        <IMaskInput className={css.formInput} mask={'+{7}(000) 000-00-00'} placeholder="Номер телефона" value={phoneNumber} onChange={(event) => setPhoneNubmer(event.target.value)} />
                        <div className={css.formBoxDateRow}>
                            <IMaskInput className={css.formInputShort} placeholder="Дата визита" mask={Date} min={new Date(1990, 0, 1)} value={dateVisit} onChange={(event) => setDateVisit(event.target.value)}/>
                            <input type="text" className={css.formInputShort} placeholder="Время визита" mask={'hh:mm'} maxLength='5' value={timeVisit} onChange={(event) => setTimeVisit(event.target.value)}/>
                        </div>
                        <input type="text" className={css.formInput} placeholder="Цель визита" maxLength='45' value={goalVisit} onChange={(event) => setGoalVisit(event.target.value)}/>

                    </div>
                    <div className={css.formBoxFooter}>
                        <div className={css.formBoxFooterCheckBox}>
                            <input type="checkbox" className={css.formBoxFooterCheckBoxInput} value={isAgreed} onChange={(event) => checkboxHandler(event)}/>
                            <span>Согласие на обработку <a href="/#/confidence" target="_blank">персональных данных</a></span>
                        </div>
                        <button className={css.formBoxFooterCheckBoxButton} 
                        onClick={() => submitForm()}
                        disabled={isAgreed && firstName && lastName && email && phoneNumber.length === 17 && dateVisit && timeVisit && goalVisit ? false : true}>Отправить</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FormPage;